.container{
    text-align: center;
    margin-top: 0.5%;
    padding: 0 2px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 4px;
    position: relative;
    max-width: 100vw;
}

.error-message {
    position: fixed;
    top: 5px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 1000;
    width: 90%;
    max-width: 300px;
    font-size: 14px;
}

.button{
    background: transparent;
    display: inline-block;
    padding: 0.2em 0.8em;
    border: 0.1em solid #FFFFFF;
    margin: 0;
    border-radius: 0.12em;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
    color: #FFFFFF;
    text-align: center;
    transition: all 0.2s;
    min-width: 90px;
    font-size: 14px;
}

.button:hover{
    color: #000000;
    background-color: #FFFFFF;
}

@media (max-width: 768px) {
    .container {
        margin-top: 0.3%;
        gap: 3px;
    }
    
    .button {
        padding: 0.15em 0.6em;
        font-size: 12px;
        min-width: 80px;
        border-width: 0.08em;
    }

    .error-message {
        top: 3px;
        width: 95%;
        font-size: 12px;
    }
}

@media (max-width: 480px) {
    .container {
        margin-top: 0.2%;
        gap: 2px;
        padding: 0 1px;
    }
    
    .button {
        padding: 0.12em 0.5em;
        font-size: 11px;
        min-width: 70px;
        border-width: 0.06em;
    }

    .error-message {
        top: 2px;
        width: 98%;
        font-size: 11px;
    }
}

.bounce{
    animation: bounce 5s infinite linear;
    position: relative;
}

@keyframes bounce {
    0%{top:0em}
    40%{top:0em}
    43%{top:-0.9em}
    46%{top:0em}
    48%{top:-0.4em}
    50%{top:0em}
    100%{top:0em;}
}